    #t_5_3.titulo-segundo
      h2 5.3 Centro de la circulacin en tanatopraxia 

    p.mb-5 
      b El centro de la circulacin en una persona viva es el corazn, 
      | este es el rgano encargado de bombear la sangre a todo el cuerpo. Cuando una persona fallece el corazn deja de latir cuando cierra las vlvulas, esto hace que la distribucin artificial se centre en otra zona. 
      b.cp El corazn en la parte superior tiene una ramificacin llamada cayado artico, que es donde se bifurcan la aorta ascendente y el cayado o arco de la aorta.

    .contenedor.mb-5
      .row
        .col.m-0.fnegro.order-lg-first.order-last
