          p.texto-blanco <em>El cayado artico contiene arterias. La arteria braquioceflica se encarga de irrigar la zona derecha de la cabeza y el brazo derecho. La arteria cartida comn izquierda irriga la zona izquierda de la cabeza.  La arteria subclavia izquierda que irriga el brazo izquierdo y un arco llamado la aorta torcica descendente que irriga las dems zonas del cuerpo.</em>
        .col-lg-7.m-0.order-first.order-lg-last
