        .col-lg-7.m-0.fnegro.contenedorNegro
          .p-5
            p.texto-blanco <em>Con base al cayado artico es que se debe pensar inicialmente para realizar la preservacin qumica arterial, por ejemplo, la arteria cartida que se ubica en el cuello y que es una arteria principal, de muy buen dimetro y cerca al corazn, se debe tener siempre como punto de partida para la inyeccin, la cual se hace en direccin al corazn para dirigir el fluido preservante en relacin al resto del cuerpo. Dependiendo de la extremidad que se inyecte como, por ejemplo, la arteria femoral o la arteria humeral ser necesario inyectar en direccin contraria para que las zonas distantes al punto de inyeccin sean preservadas.</em>

    .row.align-items-center.mb-5
      .col-lg-8
        p 
          b La ventaja de inyectar por una arteria cerca al cayado artico es que permite que las vlvulas del corazn se llenen de solucin y as se pueda generar una mejor distribucin de la solucin preservante. 
        p Comprendida la informacin anterior, 
          b.cp es importante identificar la informacin relacionada con las arterias y venas principales. 
