          b.cac vital importancia 
          b escoger el punto de inyeccin y drenaje para obtener ptimos resultados. En teora, cualquier punto de inyeccin y drenaje sirve para la preservacin vascular, 
          | pero se recomienda bsicamente dos factores a tener en cuenta:
        .col-lg-9.mx-auto
