              | Dimetro de la arteria para ingresar la cnula con la que inyectar y drenar. Realice una incisin, ni muy grande, ni muy pequea; se recomienda unos 2 o 3 cm y recuerde que la cnula dentro de la arteria va en direccin al corazn. Inserte la pinza para drenaje tambin en direccin al corazn e inyecte hacia al lado contrario (sobre todo en extremidades de los miembros superior e inferior).
      .col-lg-4.order-first.order-lg-last
