    p.mb-4 Para realizar la inyeccin y drenaje de los lquidos preservantes debe considerar la diferencia que existe en un cuerpo con necropsia, ya que sus vasos sanguneos se modifican y por ende se debe cambiar el mtodo de preservacin. En un cuerpo que ha muerto naturalmente se realiza la preservacin desde un solo punto de inyeccin y drenaje; el punto ms frecuente es la arteria cartida derecha y la vena yugular o la arteria femoral derecha y la vena femoral derecha, las cuales permiten usar mayor volumen de lquido preservante. 
