          p.texto-blanco <em>Ambas se encuentran en el cuello, comienzan desde la clavcula hasta el lbulo de la oreja. La incisin se realiza encima de la clavcula, en la parte inferior del cuello. La arteria y vena son fciles de encontrar, pues ambas poseen dimetros grandes y definidos que permiten irrigar muy bien el cuerpo.</em>
          p.texto-blanco <em>Raramente estn obstruidas, de hecho, si el cuerpo presenta complicaciones es el mejor punto de inyeccin y drenaje. </em>
          p.texto-blanco <em>Cuando utilice la cnula para inyeccin se dirige hacia el corazn para que irrigue todo el cuerpo, de ser necesario se direccionar hacia arriba para irrigar el rostro, con lo cual se debe tener cuidado para no sobreinyectar esa regin y deshidratarla ms de lo necesario. En caso tal, podr usar una solucin diferente para el rostro y otra para el cuerpo, la nica desventaja es que por su ubicacin queda la incisin muy visible. Se recomienda no hacerla tan larga para que al cerrarla se vea estticamente bien.  Selle con pegamento y aplicando algodn o material absorbente, no es necesario suturar a menos que el cuerpo necesite un sellado completo por presencia de edema, se recomienda que la incisin sea de unos 2 cm en promedio, pero que tenga el espacio suficiente para que el drenaje sea continuo y sin obstruccin de piel o msculos.</em>
        .col-lg-7.m-0.order-first.order-lg-last
