        .col-lg-7.m-0.fnegro.contenedorNegro
          .p-5
            p.texto-blanco <em>Son continuaciones de la arteria subclavia y a pesar de encontrarse cerca al corazn, lo que permite irrigar todo el cuerpo y ocultar la incisin fcilmente, presenta algunos inconvenientes como en el drenaje que no es tan bueno.</em>
            p.texto-blanco <em>Estas arterias y venas son buenas cuando se debe aplicar la preservacin localizada,  pero no cuando se aplican grandes volmenes de solucin preservante que requieran grandes volmenes de drenaje. Se debe direccionar hacia el corazn para que irrigue todo el cuerpo.  Si se seleccionan se debe irrigar hacia el lado contrario para que irrigue la extremidad de la mano; sin embargo, con el solo direccionamiento hacia el corazn la mano queda con una irrigacin completa, cuyo factor adems hace que se deshidrate y fije la piel del brazo hasta el punto de verse poco esttico y completamente rgido; por presentar tantas ramificaciones suele tener problemas de drenaje por la sobrecarga de fluido en la extremidad; el dimetro de la arteria y de la vena son de dimetros muy pequeos, para localizarla se debe extender el brazo.</em>
      .row
        .col.m-0.fnegro.order-lg-first.order-last.contenedorNegro
