            p.texto-blanco <em>Es el segundo punto de inyeccin y drenaje ms recurrente; es la continuacin de las arterias ilacas. Al ubicarse la arteria hacia al corazn irriga muy bien todo el cuerpo. Se debe dirigir tambin a la pierna para irrigar, el dimetro de ambas es grande y la incisin queda oculta, son relativamente fciles de encontrar; sin embargo, en casos de cuerpos obesos se complica un poco, a pesar de no ser tan cercano al corazn tiene muy buenos resultados.</em>
