    p.mb-5 La preservacin qumica como se ha mencionado tiene diferentes formas de ejecutarse. A continuacin, se describen los tipos de inyeccin que se usan y algunas tcnicas a las que se puede recurrir en caso de complicaciones en la preservacin qumica.

