            p Es la ms comn de las inyecciones cuando se trata de cuerpos sin necropsia, es decir, muerte natural. Esta consiste en seleccionar un punto de inyeccin y drenaje para el proceso de preservacin qumica. Normalmente el ms usado para este tipo de inyeccin es:
            .row.align-items-center.justify-content-center
