          div(numero='2' titulo='Inyeccin / drenaje mltiple')
            p Se realiza por seis puntos (2 arterias cartidas, derecha e izquierda; 2 arterias axilares o arterias humerales, derecha e izquierda; 2 arterias femorales, derecha e izquierda). Debe implementarse cuando: 
            ol.lista-ol--cuadro.lista-ol--separador
