              li 
                .lista-ol--cuadro__vineta.fcp
                  span 2
                | Tiempo de fallecimiento. 
              li 
                .lista-ol--cuadro__vineta.fcp
                  span 3
                | Signos de descomposicin. 
              li 
                .lista-ol--cuadro__vineta.fcp
                  span 4
                | La arteria inicial se daa. 
              li 
                .lista-ol--cuadro__vineta.fcp
                  span 5
                | El cuerpo evidentemente muestra que no inyect una zona en especfico. 
              li 
                .lista-ol--cuadro__vineta.fcp
                  span 6
                | El cuerpo presenta gas en el tejido. 
              li 
                .lista-ol--cuadro__vineta.fcp
                  span 7
                | El cadver se debe embalsamar por segunda vez. 
              li 
                .lista-ol--cuadro__vineta.fcp
                  span 8
                | El cuerpo presenta necropsia o donacin de rganos y tejidos.
          div(numero='3' titulo='Inyeccin dividida')
            p Es aquella en la que se realiza la inyeccin arterial en una ubicacin diferente a la del drenaje.  Un ejemplo es inyectar desde la arteria cartida y drenar desde la vena femoral.
          div(numero='4' titulo='Inyeccin cervical restringida')
            p Se caracteriza por la exploracin de ambas arterias cartidas, para que la cabeza y el rostro se inyecten con una concentracin de qumico preservante diferente a la del cuerpo. Se inyecta por una y se cierra el paso de la otra para impedir que grandes cantidades de lquido preservante ingresen al rostro.  Esto genera mayor control en la preservacin. Se recomienda ser usada cuando el cuerpo presenta:
            ol.lista-ol--cuadro.lista-ol--separador
              li 
                .lista-ol--cuadro__vineta.fcp
                  span 1
                | Se necesita una solucin fuerte en el cuerpo, pero no en el rostro.
              li 
                .lista-ol--cuadro__vineta.fcp
                  span 2
                | Existencia de tumores en la zona de rostro y cuello.
              li 
                .lista-ol--cuadro__vineta.fcp
                  span 3
                | El cuerpo presenta ictericia. 
              li 
                .lista-ol--cuadro__vineta.fcp
                  span 4
                | Se necesita usar presiones diferentes en el rostro y en el cuerpo.
          .row.align-items-center(numero='5' titulo='Inyeccin hipodrmica')
            .col-lg-7
              p Es un mtodo que se realiza directamente en los msculos y tejidos que no hayan recibido qumico preservante mediante la solucin arterial. Se hace de manera localizada. Esta se implementa cuando los cuerpos han recibido necropsia, presenta zonas con gangrena o tumores. La ejecucin de este tipo de inyeccin requiere el uso de agujas puntiagudas y de buen calibre. Se recomienda realizarse con una solucin preservante de alto ndice y en zonas que no eran visibles, pues el qumico se marcar de manera evidente.
            .col-lg-5
              figure
                img(src='@/assets/curso/t5/f25.svg', alt='imagen relacionada')
          .row.align-items-center(numero='6' titulo='Inyeccin continua')
            .col-lg-3
              figure
                img(src='@/assets/curso/t5/f26.svg', alt='imagen relacionada')
            .col-lg-9
              p Consiste en la inyeccin y el drenaje en simultneo sin ningn tipo de restriccin, ni control. No se recomienda debido a los cortocircuitos vasculares. Una de las excepciones podra ser los casos de cuerpos con ictericia.
          div(numero='7' titulo='Inyeccin intermitente')
            p Consiste en inyectar continuamente el cuerpo; pero con el drenaje a pequeos intervalos, es decir, abriendo y cerrando el drenaje. Para esto apoye uno de los dedos o haga presin en la pinza de drenaje para impedir que salga la sangre.
    
    p En la inyeccin arterial existen signos o indicadores que permiten establecer si el qumico preservante est ingresando en el cuerpo. 
    TabsC.color-primario
      .p-5(titulo='Coloracin del tejido con los tintes del lquido preservante')
        .row.align-items-center
          .col-lg-7
            h3 Coloracin del tejido con los tintes del lquido preservante
            p Es uno de los signos ms visibles de la irrigacin vascular, ya que las zonas por las que pasa el qumico preservante tomarn la coloracin presente en la solucin preservante; por lo general, para la inyeccin vascular son qumicos con coloraciones en tonos rojizos que le darn un aspecto cosmtico a la piel del cadver.
          .col-lg-5
            figure
              img(src='@/assets/curso/t5/f27.png', alt='imagen relacionada')
      .p-5(titulo='Distensin de vasos sanguneos')
        .row.align-items-center
          .col-lg-7
            h3 Distensin de vasos sanguneos
            p Se puede observar una pequea inflamacin de los vasos sanguneos de la sien o de las manos por la irrigacin o el drenaje. Para dar cuenta de ello, el tanatopractor debe observar muy bien el antes, el durante y el despus de la preservacin arterial para dar cuenta de esta caracterstica.
          .col-lg-5
            figure
              img(src='@/assets/curso/t5/f28.png', alt='imagen relacionada')
      .p-5(titulo='Drenaje evidente')
        .row.align-items-center
          .col-lg-7
            h3 Drenaje evidente
            p Si existe un correcto intercambio de fluidos a nivel vascular, el drenaje se har evidente y saldr una buena cantidad de sangre y lquido preservante.
          .col-lg-5
            figure
              img(src='@/assets/curso/t5/f29.png', alt='imagen relacionada')
      .p-5(titulo='Fijacin y prdida de elasticidad en los tejidos')
        .row.align-items-center
          .col-lg-7
            h3 Fijacin y prdida de elasticidad en los tejidos
            p Entre ms qumico preservante ingrese a las guas vasculares ms se comienza a reafirmar los tejidos y la piel. Este tipo de endurecimiento diferente al rigor mortis es causado con la concentracin usada de qumico preservante; muchas veces no se ve inmediatamente sino con el pasar de las horas luego de la preservacin.
          .col-lg-5
            figure
              img(src='@/assets/curso/t5/f30.png', alt='imagen relacionada')
      .p-5(titulo='Eliminacin de las decoloraciones de sangre')
        .row.align-items-center
          .col-lg-7
            h3 Eliminacin de las decoloraciones de sangre
            p Las livideces o los tejidos con presencia de sangre visible comienzan a removerse gracias a la presin de la mquina arterial y del lquido preservante ingresando por todo el sistema vascular, este es uno de los mejores signos para confirmar que el lquido preservante est ingresando, removiendo sangre y cogulos.
          .col-lg-5
            figure
              img(src='@/assets/curso/t5/f31.png', alt='imagen relacionada')

    Separador
    #t_5_5.titulo-segundo
      h2 5.5 Movimiento de los fluidos

    .row.align-items-center.bg9.afterCP1.p-5.mb-5.sm-fg
      .col-lg-4
      .col-lg-1(data-aos="fade-right")
        figure
          img(src='@/assets/curso/icon-admiracion.svg' alt='Smbolo de atencin').w85px.noMobile
        img(src='@/assets/curso/t5/bg9f1.png' alt='Smbolo de atencin').mb-4.onlyMobile
      .col-lg-6
        p.bordeLnegro.mb-0 Cuando se habla de movimientos de los fluidos en un cuerpo sin vida se hace referencia al sistema circulatorio, el cual es dinmico. Se compone de vas principales y vas secundarias que crean ramificaciones que se dividen en mltiples veces y que llegan a todas las partes del cuerpo. Estas ramificaciones se diferencian entre grosores y dimetros de las arterias y venas.

    .row.mb-5
      .col-lg-7
        p Durante la preservacin arterial suceden en simultneo cuatro acciones que se hacen para garantizar la preservacin y sanitizacin de los tejidos. Estas son:
        ol.lista-ol--cuadro.lista-ol--separador
          li 
            .lista-ol--cuadro__vineta.fcp
              span 1
            | Inyeccin arterial del qumico preservante.
          li 
            .lista-ol--cuadro__vineta.fcp
              span 2
            | Distribucin del lquido preservante por las arterias.
          li 
            .lista-ol--cuadro__vineta.fcp
              span 3
            | Difusin del lquido preservante por las arterias.
          li 
            .lista-ol--cuadro__vineta.fcp
              span 4
            | Drenaje de los lquidos presentes en las guas vasculares como sangre y solucin preservante.
        p El movimiento de los fluidos dentro del sistema circulatorio puede presentar diferentes presiones por la naturaleza misma del recorrido, todo el sistema vascular presenta diferentes calibres y dimetros en toda la extensin del cuerpo, adems los lquidos preservantes tienen densidades diferentes a la sangre que circula en vida por las guas vasculares; luego de morir la persona, la sangre puede estar ms lquida o ms espesa y/o con presencia de cogulos en diferentes zonas del cuerpo. Una consideracin importante que es todo el sistema circulatorio (arterias, venas, arteriolas, capilares, vnulas, etc.) tienen tamaos y dimetros diferentes, lo que hace que el movimiento de los fluidos no sea uniforme, pues los lquidos pasan por canales ms estrechos o amplios generando una fuerza diferente.
      .col-lg-5.order-last.order-lg-first
        figure
          img(src='@/assets/curso/t5/f32.png', alt='imagen relacionada')

    p Para comprender un poco mejor el movimiento de los fluidos es necesario tener en cuenta la definicin de los siguientes trminos:
    .row.px-5.mb-5
      .col.mmw.p-4.text-center.bt.bt--cp.tarjeta--gris.mx-2
        figure
          img(src='@/assets/curso/t5/f33.svg', alt='imagen relacionada').w85px.mx-auto.mb-4
        h4 Cohesin
        p Es la resistencia que hace un lquido en un medio que lo contenga. 
      .col.mmw.p-4.text-center.bt.bt--cac.tarjeta--gris.mx-2
        figure
          img(src='@/assets/curso/t5/f34.svg', alt='imagen relacionada').w85px.mx-auto.mb-4
        h4 Arrastre
        p Es la fuerza ejercida por el fluido contra la superficie donde se mueve.
      .col.mmw.p-4.text-center.bt.bt--cp.tarjeta--gris.mx-2
        figure
          img(src='@/assets/curso/t5/f35.svg', alt='imagen relacionada').w85px.mx-auto.mb-4
        h4 Presin
        p Es la medida volumtrica respecto a la intensidad que se ejerce contra una masa.
      .col.mmw.p-4.text-center.bt.bt--cac.tarjeta--gris.mx-2
        figure
          img(src='@/assets/curso/t5/f36.svg', alt='imagen relacionada').w85px.mx-auto.mb-4
        h4 Rango de flujo (caudal)
        p Es el volumen o cantidad total de un fluido en un lapso determinado.

    p.mb-4 Al conocer el movimiento de los fluidos por los canales vasculares se debe hablar de la difusin de la solucin arterial de preservacin, para comprender mejor cmo llega a cumplir su propsito de preservacin. Esta se define como el paso de algunos elementos de las soluciones de embalsamamiento inyectadas desde dentro del capilar (intravascular) hacia los espacios tisulares (extravasculares) (Mayer, 2006, p. 281). 
    .row.mb-4
      .col-lg-4
        figure
          img(src='@/assets/curso/t5/f37.png', alt='imagen relacionada')
      .col-lg-8
        p Inicia con una red de capilares sanguneos, estos son estructuras corporales tan pequeas (5-10 micras) que solo podrn verse por microscopio, aunque esto no les impide acumular una importante cantidad de lquido dentro de ellos y esto se produce por la filtracin de la solucin preservante a travs de la permeabilidad de las paredes de los capilares; pero para llenarlos por completo se hace necesario la remocin de las congestiones de sangre y decoloracin para permitir la saturacin y preservacin de los tejidos. Luego de pasar por los capilares, la solucin arterial pasar con el lquido intersticial que rodea cada clula y finalmente a las clulas del cuerpo, donde reaccionar con las protenas que all se encuentran. 
    
    .cajon.color-secundario.p-4.mb-5
      p.mb-0 La energa necesaria para hacer circular las soluciones de embalsamamiento se crea a partir de un mecanismo no viviente, ya que este puede mover los materiales a travs de las membranas muertas, capilares. Estos procesos de transporte son: 

    .tarjeta-avatar-b.mb-4
      .tarjeta-avatar-b__img(data-aos="fade-right")
        img.mx-auto(src='@/assets/curso/t5/f38.svg' alt='Seal de atencin' style='width:155px;')
      .tarjeta.tarjeta-white.tarjeta-white--color-red
        .p-4.ps-0.py-5
          p.mb-0 Filtracin a presin, en este proceso los solutos y solventes de los lquidos arteriales pasan al lquido intersticial y los dems ingredientes de los lquidos (humectantes y tensoactivos) reducen la tensin superficial, logrando as que la solucin arterial sea ms hmeda y pueda pasar ms fcilmente por las membranas de los capilares.
    .tarjeta-avatar-b.mb-4
      .tarjeta-avatar-b__img(data-aos="fade-right")
        img.mx-auto(src='@/assets/curso/t5/f38.svg' alt='Seal de atencin' style='width:155px;')
      .tarjeta.tarjeta-white.tarjeta-white--color-red
        .p-4.ps-0.py-5
          p.mb-0 Osmosis, este proceso implica el movimiento del solvente (agua) desde una solucin diluida a una ms concentrada, a travs de una membrana semipermeable como las paredes de los capilares.
    .tarjeta-avatar-b.mb-4
      .tarjeta-avatar-b__img(data-aos="fade-right")
        img.mx-auto(src='@/assets/curso/t5/f38.svg' alt='Seal de atencin' style='width:155px;')
      .tarjeta.tarjeta-white.tarjeta-white--color-red
        .p-4.ps-0.py-5
          p.mb-0 Dilisis, es la difusin de los solutos disueltos en una solucin que atraviesa una membrana semipermeable.

    Separador
    #t_5_6.titulo-segundo
      h2 5.6 Control de flujo y presin de la mquina inyectora

    p.mb-5 Para realizar una ptima preservacin qumica va arteria es necesario el uso de herramientas externas que faciliten el ingreso y el recorrido por las guas vasculares, por medio de una presin que supere la presin interna. Si bien es cierto que se puede realizar por medio de la gravedad, esta no controla ni la presin, ni el flujo con el que sale; por lo tanto, para ejecuciones profesionales solo se recomienda la mquina inyectora con la que se puede tener mayor precisin y manejo de los fluidos que entran al sistema circulatorio.  Revisa el siguiente recurso educativo para mayor informacin.
    .tarjeta.tarjeta--gris.p-5.mb-5
      .row.align-items-center.justify-content-around
        .col-lg-3
          figure
            img(src='@/assets/curso/t4/f32.svg', alt='imagen relacionada')
        .col-lg-6
          .titulo-segundo
            h2 Mquina inyectora
          p Los qumicos para el embalsamamiento se pueden clasificar en cavitales (cavidad), especiales y blanqueadores. Para ampliar el conocimiento acerca de este tema consultar el siguiente recurso.
          a.boton.color-acento-botones(:href="obtenerLink('/downloads/Maquina_inyectora.pdf')" target="_blank" type="application/pdf")
            span Descargar
            i.fas.fa-file-download

    Separador
    #t_5_7.titulo-segundo
      h2 5.7. Mecnica de la inyeccin vascular

    p.mb-5 La preservacin qumica cuenta con una parte mecnica que est directamente relacionada con la inyeccin de fluidos en el sistema vascular; esto consiste en aplicar un fluido preservante por las arterias y que haya algo que pueda hacerlo circular dentro del sistema vascular, en vida el centro de la circulacin es el corazn, que es el encargado de bombear la sangre por todo el cuerpo; pero en muerte se necesita una mquina inyectora para que pueda  empujar el lquido preservante por todo el cuerpo.
    .tarjeta--container.row.mb-5
      .col-md.tarjeta.color-secundario.p-5.text-center
        .row.justify-content-center.mb-4
          .col-6
            figure
              img(src='@/assets/curso/t5/f39.svg', alt='imagen relacionada')
        h2.cst Preservacin arterial
        p.cst Es el ingreso de qumicos preservantes, con esta inyeccin vascular no solo se logra la preservacin temporal del cadver sino tambin la sanitizacin y restauracin de los tejidos del cuerpo. Es all donde se realiza el intercambio de fluidos a nivel vascular, pues a medida que el lquido ingresa por las arterias la sangre se drena por las venas hacia el exterior.
      .col-md.tarjeta.color-primario.p-5.text-center
        .row.justify-content-center.mb-4
          .col-6
            figure
              img(src='@/assets/curso/t5/f40.svg', alt='imagen relacionada')
        h2.cst Drenaje vascular
        p.cst Es la evacuacin de la sangre y de los fluidos del sistema vascular. Este proceso se hace exclusivamente por las venas porque son el canal, opuesto al sistema arterial, con la mayor cantidad de sangre. El drenaje se usa como referencia al paralelo de las guas arteriales.

    p.mb-5 La inyeccin y el drenaje son procesos que ocurren en simultneo. Cuando el lquido preservante ingresa al sistema vascular por las arterias, el lquido que se drena sale como una mezcla entre sangre y cogulos con lquido preservante y lquido intersticial. Para entender esta mecnica de inyeccin y drenaje vascular es importante dejar claros algunos conceptos que son fundamentales al momento de la preservacin qumica arterial. Le sugiero leerlos a continuacin.

    .row.align-items-center
      .col-lg-5
        figure
          img(src='@/assets/curso/t5/f41.svg', alt='imagen relacionada')
      .col-lg-7
        ol.lista-ol--cuadro.lista-ol--separador
          li 
            .lista-ol--cuadro__vineta.fcp
              span 1
            | El fluido tiene que entrar al sistema vascular a travs de las arterias. Esto permite desplazar la sangre del sistema circulatorio y movilizar los obstculos o bloqueos que haya dentro del sistema. Su finalidad es que los fluidos preservantes lleguen a los capilares y lograr la preservacin.
          li 
            .lista-ol--cuadro__vineta.fcp
              span 2
            | El fluido debe salir o drenarse por las venas para que se produzca el intercambio de fluidos preservantes. El drenaje cumple una funcin de movilizar los lquidos por fuera del cuerpo. Su contribucin tambin ayuda a que las decoloraciones sanguneas intravasculares se eliminen o atenen.
          li 
            .lista-ol--cuadro__vineta.fcp
              span 3
            | El fluido preservante que ingresa por las arterias debe mantener cierta fuerza y continuidad para que logre desplazarse por todo el sistema vascular.
          li 
            .lista-ol--cuadro__vineta.fcp
              span 4
            | La inyeccin vascular se hace en direccin al corazn para que se distribuyan los qumicos por el resto del sistema circulatorio. Si es un caso de necropsia al estar el sistema circulatorio interrumpido por esta, se realiza inyeccin a cada extremidad de manera distal.

    Separador
    #t_5_8.titulo-segundo
      h2 5.8 Resistencias vasculares 

    .row.align-items-center.mb-4
      .col-lg-8
        p El cuerpo con los cambios que sufre en la muerte genera resistencia que afecta la correcta movilizacin y distribucin de la solucin arterial. Existen dos tipos de resistencias en el cuerpo dentro de las guas vasculares: resistencia intravascular y resistencia extravascular, a cualquiera de estas resistencias se le conoce como cortocircuito vascular. Normalmente en la inyeccin de la solucin preservante toma diferentes vas vasculares para irrigar todo el cuerpo, los lquidos tienden a tomar las vas de menor resistencia que, por lo general son las que llevan a la piel, pero se  necesita de una pequea cantidad de resistencia para que el lquido penetre bien en los capilares o los tejidos ms profundos, pues si no existiere resistencia la solucin pasara derecho por las guas vasculares como si se tratase de un lavado y sin remover o empujar cogulos de sangre.
      .col-lg-4.order-first.order-lg-last
        figure
          img(src='@/assets/curso/t5/f42.png', alt='Cambios que sufre el cadver')

    .bloque-texto-a.color-secundario.p-4.p-md-5.mb-5 
      .row.m-0.align-items-center.justify-content-between
        .col-lg-4.mb-4.mb-lg-0
          figure(data-aos="fade-right")
            img(src='@/assets/curso/t5/f43.svg' alt='Laboratorio de tanatopraxia')
        .col-lg-8
          .bloque-texto-a__texto.p-4
            p.mb-0 Los cadveres en general presentan una serie de combinacin de resistencias vasculares tanto internas como externas, por lo tanto, a medida que progresa la preservacin arterial el tanatopractor debe estar atento a las modificaciones o cambios que se generan en el cuerpo, haciendo un anlisis del caso, a medida que avanza la preservacin ayuda a asegurar que realmente el cuerpo est siendo bien preservado. A continuacin, se presenta una descripcin de las resistencias antes mencionadas.

    .col-lg-10.mx-auto
      LineaTiempoD.color-primario.mb-5
        .row.algin-items-center(numero='1' titulo='Las resistencias intravasculares')
          .col-lg-4
            figure(data-aos="fade-right")
              img(src='@/assets/curso/t5/f44.png' alt='Corto circuito por coagulacin')
              figcaption En la presente imagen usted puede observar un cortocircuito por coagulacin.
          .col-lg-8
            p Esta resistencia es la ms importante, pues contempla los dimetros o tamaos de las arterias por donde ingresar el lquido preservante. Cuando es por reduccin o estrechamiento de las arterias es muy poco lo que se puede hacer para eliminar la resistencia. Se debe tener mucho cuidado que al introducir la cnula arterialmente no dae la pared interna de ella. A medida que la irrigacin habr zonas donde se estrecha, se usa una mquina que genere la presin en el sistema vascular para movilizar todos los fluidos presentes en l.
        div(numero='2' titulo='La resistencia extravascular')
          p Es aquella que ocurre por fuera de las guas vasculares. Estas se asocian al cuerpo en general como el peso, el tejido adiposo, etc. El tanatopractor tiene ms posibilidades de manejar estas resistencias por ser externas al sistema vascular. Algunos casos son:
          .tarjeta-avatar-b.mb-4
            .tarjeta-avatar-b__img(data-aos="fade-right")
              img.mx-auto(src='@/assets/curso/t5/f45.svg' alt='Seal de atencin' style='width:100px;')
            .tarjeta.tarjeta-white.tarjeta-white--color-red
              .p-4.ps-0.py-5
                h4 Rigor mortis
                p.mb-0 Se recomienda antes de iniciar el proceso de preservacin hacer masajes para romper la rigidez cadavrica. Cuando se inyecta y se drena el lquido preservante se recomienda hacer masajes con crema humectante o jabn lquido, estimulando las guas vasculares.
          .tarjeta-avatar-b.mb-4
            .tarjeta-avatar-b__img(data-aos="fade-right")
              img.mx-auto(src='@/assets/curso/t5/f45.svg' alt='Seal de atencin' style='width:100px;')
            .tarjeta.tarjeta-white.tarjeta-white--color-red
              .p-4.ps-0.py-5
                h4 Gas en cavidades
                p.mb-0 Cuando el cuerpo ha iniciado el proceso de descomposicin en la cavidad abdominal se crea una presin extra que oprime la arteria aorta descendente y la vena cava. Para ello, es recomendado hacer una pequea incisin con un bistur en el rea abdominal para liberar los gases.

    .tarjeta-avatar-b.mb-4
      .tarjeta-avatar-b__img(data-aos="fade-right")
        img.mx-auto(src='@/assets/curso/if7.svg' alt='Seal de atencin' style='width:155px;')
      .tarjeta.tarjeta-white.tarjeta-white--color-red
        .p-5
          p.mb-0 Adems de los casos mencionados, en el proceso de embalsamamiento puede encontrar algunos obstculos que influyen en las tcnicas de tanatopraxia.  Estas situaciones pueden estar asociadas a la edad del cuerpo fallecido, efecto de los medicamentos, descomposicin del cadver y patologas.

